.logo {
  text-align: center;
}

.list {
  width: 300px;
  list-style: none;
  margin: 0 auto;
  padding: 0;
  text-align: center;
}

.listItem {
  margin-bottom: 40px;
}

.contacts {
  margin-top: 100px;
  margin-bottom: 100px;
  text-align: center;
  color: #9c1e1e;
}

.map {
  height: 150px;
  width: 300px;
  line-height: 150px;
  margin: 0 auto;
  border: 1px solid rgb(34, 34, 124);
  text-align: center;
  color: rgb(34, 34, 124);
}

.footer {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  width: 100%;
  text-align: center;
}

@media (min-width: 768px) {
  .list {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }

  .listItem {
    width: 28%;
  }

  .footer {
    /* position: fixed; */
    margin: 0;
    padding: 0;
    bottom: 20px;
    right: 30px;
    flex-direction: row;
    justify-content: flex-end;
    align-self: flex-end;
    flex: 0 0 100%;
  }
}
